import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import { STORE_KEY } from '../../constants';
import { SEGMENT_DURATION } from '../../constants';

const secConfiguration = {
  '1': {
    stickCount: 11,
    value: 1,
  },
  '5': {
    stickCount: 11,
    value: 1,
  },
  '10': {
    stickCount: 4,
    value: 2,
  },
  '15': {
    stickCount: 4,
    value: 3,
  },
  '20': {
    stickCount: 6,
    value: 5,
  },
  '25': {
    stickCount: 6,
    value: 5,
  },
  '30': {
    stickCount: 6,
    value: 5,
  },
};

import { getCurrentSegmentRange, getSegmentWidth } from '../../selectors';

class Second extends PureComponent {
  render() {
    const { size, value } = this.props;
    const seconds = new Array(size).fill(null);

    return (
      <div className="second">
        <div className="second_value">{value}</div>
        <div className="second_line_group">
          {seconds.map((item, idx) => <i key={idx} className="second_line" />)}
        </div>
      </div>
    );
  }
}

class Segment extends PureComponent {
  // TODO: optimize parser
  parseTime = time => {
    let arrTime = [];

    arrTime[0] = 0 | ((time / 3600) % 24); // hours
    arrTime[1] = 0 | ((time / 60) % 60); // minutes
    arrTime[2] = 0 | (time % 60); // seconds

    arrTime = arrTime.map(item => {
      if (item < 10) {
        return '0' + item;
      }

      return item;
    });

    const day = 0 | (time / (3600 * 24));

    return (day > 0 ? 'd' + day + ' ' : '') + arrTime.join(':');
  };

  render() {
    const { segmentIndex, segmentWidth, epochSize } = this.props;

    const seconds = new Array(
      SEGMENT_DURATION / secConfiguration[epochSize].value
    ).fill(null);

    return (
      <div
        className="segment"
        style={{
          width: segmentWidth,
          transform: `translateX(${segmentWidth * segmentIndex}px)`,
        }}
      >
        {seconds.map((second, idx) => {
          const value = this.parseTime(
            segmentIndex * SEGMENT_DURATION +
              idx * secConfiguration[epochSize].value
          );

          return (
            <Second
              size={secConfiguration[epochSize].stickCount}
              key={idx}
              value={value}
            />
          );
        })}
      </div>
    );
  }
}

class Ruler extends PureComponent {
  render() {
    const { segmentRange, segmentWidth, epochSize } = this.props;

    return (
      <div className="ruler">
        {segmentRange.map(segment => (
          <Segment
            epochSize={epochSize}
            key={segment}
            segmentIndex={segment}
            segmentWidth={segmentWidth}
          />
        ))}
      </div>
    );
  }
}

const mapStateToProps = state => ({
  segmentWidth: getSegmentWidth(state),
  segmentRange: getCurrentSegmentRange(state),
  epochSize: state.navigation.epochSize,
});

export default connect(mapStateToProps, undefined, undefined, {
  storeKey: STORE_KEY,
})(Ruler);
